import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'

export default {
  props: ["tagName", "tagSlug", "fetchURL"],
  components: {Slick},


  data(){
    return {
      slickArrows: {
        nextArrow: '.next_arrow.' + this.tagSlug + 'Slick',
        prevArrow: '.prev_arrow.' + this.tagSlug + 'Slick'
      },
      tagPrevOptions: {
        slidesToShow: 14,
        slidesToScroll: 4,
        rows: 2,
        infinite: false,
        variableWidth: true,
        // centerMode: true,
        prevArrow: `<button class="prev_arrow ${this.tagSlug}Slick" ref="catPrePrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow ${this.tagSlug}Slick" ref="catPreNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 576,
            settings: {
              slidesToShow: 5,
              slidesToScroll: 2,
              infinite: false,
              rows: 1
            }
          },
        ]
      }

    }
  },

  methods: {
    tagPrevBeforeChange(event, slick, currentSlide, nextSlide) {
      if (nextSlide == slick.$slides.length-12){
        $(this.slickArrows.nextArrow).hide();
      } else {
        $(this.slickArrows.nextArrow).show();
      }
      if (nextSlide == 0){
        $(this.slickArrows.prevArrow).hide();
      } else {
        $(this.slickArrows.prevArrow).show();
      }
    },
    tagPrevInit() {
      $(this.slickArrows.prevArrow).hide();
    },
    

  },

}
</script>

<style lang="sass" scoped>
.tagPrev
  &-header
    border-radius: 8px 8px 0 0
    background-color: $green
    height: 65px
    display: flex
    align-items: center
    justify-content: space-between
    & h2 
      padding: 0 30px
      margin: 0
      color: $almost_white
      font-family: Montserrat
      font-style: normal
      font-weight: 600
      font-size: 22px
      line-height: 22px
    & a 
      padding: 15px 30px
      color: $dark_green
      font-family: Montserrat
      font-style: normal
      font-weight: bold
      font-size: 18px
      line-height: 22px
      text-decoration: none
  &-content
    width: 100%
    padding: 15px 0
    &__item
      width: 119px !important
      display: flex !important
      border-radius: 8px
      margin-bottom: 15px
      position: relative
      overflow: hidden
      cursor: pointer
      &:hover
        & .tagPrev-content__item-title
          bottom: 0
      & img 
        object-fit: cover
        width: 100%
        height: 100%
      &-title
        position: absolute
        bottom: -90px
        padding: 10px
        background-color: $title_hover
        border-bottom-left-radius: 8px
        border-bottom-right-radius: 8px
        transition: .4s all ease
        
        & p
          text-align: center
          padding: 0
          margin: 0
          font-family: Montserrat
          font-style: normal
          font-weight: 500
          font-size: 14px
          line-height: 16px
          color: $light_green
      & a 
        width: 100%
        height: 100%
        position: absolute


// @media screen and (max-width: 575.98px)
